@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Montserrat:wght@400;700&display=swap');

*{
  font-family : Arial, Helvetica, sans-serif
}

body {
  font-family: 'Montserrat', sans-serif;
}

h1, h2, p {
  font-family: 'Playfair Display', serif;
}

.logos{
  margin : 10px 20px;
  width:250px;
}

.header p{
  font-size : 0.5em;
  display : inline;
}

.info p{
  font-size : 1em;
  font-family : Arial, Helvetica, sans-serif;
}

p.sub-header{
  font-family: 'Playfair Display', serif;
  font-size : 20px;
  margin : 10px;
}

.summary{
  display : flex;
  margin : 0 20px;
}

body{
  margin : 0;
}

.App{
  display : grid;
  grid-template-areas:
    "header header header header"
    "form form form form"
    "graph graph graph graph";
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows : 0.1fr 0.1fr 0.5fr;
  gap : 20px;
}

.header{
  grid-area : header;
  border-bottom : 3px solid rgb(190, 189, 184);
  font-style : italic;
  font-size : 25px;
  display : flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.graphPlot{
  grid-area : graph;
}

h1, h2, p {
  margin : 10px;
  flex-grow : auto;
}

p{
  font-weight : bold;
  margin-bottom : 5px;
}

.info{
  /* border : 3px solid rgb(190, 189, 184); */
  grid-area : form;
}

.none{
  grid-area : none;
}

.blank{
  grid-area : blank;
}

input[type='number'], button[type='submit']{
  width: fit-content; /* Adjust the width of the input */
  padding: 10px; /* Add some padding for better readability */
  margin: 5px; /* Add margin around the inputs */
  border-radius: 5px; /* Rounded corners */
  border: 1px solid #ccc; /* Border color */
  background-color: #f8f8f8; /* Light grey background */
  color: #333; /* Text color */
}

button[type='submit']{
  width : fit-content;
  margin-left : 20px;
  cursor : pointer;
}

button:active{
  background-color: #34354175;
  outline : none;
}

input[type='number']::placeholder {
  color: #888; /* Color of the placeholder text */
}

input[type='number']:focus {
  border-color: #0066cc; /* Border color when the input is focused */
  outline: none; /* Remove the default focus outline */
  box-shadow: 0 0 5px rgba(0, 102, 204, 0.5); /* Add a glow effect on focus */
}

@media (max-width: 768px) {
  .header, .info, .summary, .graphPlot {
    margin: 10px 5px;
  }

  .header {
    font-size: 20px; /* Smaller font size for header */
  }

  p, h1, h2 {
    font-size: 14px; /* Smaller font sizes */
  }

  .logos {
    width: 150px; /* Smaller logo */
  }

  .App {
    display: block; /* Stack elements instead of using grid */
  }

  input[type='number'], button[type='submit'] {
    padding: 8px; /* Slightly smaller padding */
    margin: 3px; /* Smaller margin */
  }

  p.sub-header {
    font-size: 18px; /* Smaller sub-header font size */
  }

  .summary label{
    display : block;
  }
}